import { Divider, Grid2, Typography } from "@mui/material"
import { DefaultCarousel } from "../../../../components/controles/default-carousel/default-carousel"
import { useConteudoComponentStyles } from "./conteudo-component-styles"
import { DownloadIcon } from "../../../../components/icons/download-icon";
import { DefaultCard } from "../../../../components/controles/default-card/default-card";
import { DefaultButton } from "../../../../components/controles/default-button/default-button";
import { InformationIcon } from "../../../../components/icons/information-icon";
import { useNavigate } from "react-router-dom";
import { useCallback } from "react";

export const ConteudoComponent = () => {

    //AUX
    const classes = useConteudoComponentStyles();
    const navigate = useNavigate();

    const clickDownloads = useCallback(() => {
        return navigate('/downloads');
    }, [navigate]);

    const clickSobreNos = useCallback(() => {
        return navigate('/sobre-nos');
    }, [navigate]);

    return (
        <>
            <Grid2 container spacing={2} className={classes.root}>
                <Grid2 size={12}>
                    <Grid2 container>
                        <Grid2 size={12}>
                            <Typography
                                className={classes.tittle}
                            >
                                ACAP - Futsal
                            </Typography>
                        </Grid2>
                        <Grid2 size={12}>
                            <Divider variant="fullWidth" />
                        </Grid2>
                    </Grid2>
                </Grid2>
                <Grid2 size={12}>
                    <DefaultCarousel />
                </Grid2>
                <Grid2 size={12}>
                    <Grid2 container>
                        <Grid2 size={12}>
                            <Typography
                                className={classes.tittle}
                            >
                                Downloads
                            </Typography>
                        </Grid2>
                        <Grid2 size={12}>
                            <Divider variant="fullWidth" />
                        </Grid2>
                    </Grid2>
                </Grid2>
                <Grid2 size={8}>
                    <Typography
                        className={classes.texto}
                    >
                        Na sesso de downloads, voc encontrar as atualizaes mais recentes dos aplicativos ACAP para Web e Desktop. Essas atualizaes so cuidadosamente desenvolvidas e disponibilizadas pela nossa equipe para garantir a melhor experincia aos usurios. Sempre que uma nova verso estiver pronta, ela ser disponibilizada aqui, permitindo que voc tenha acesso s ltimas melhorias e funcionalidades dos nossos aplicativos.
                    </Typography>
                </Grid2>
                <Grid2 size={4}>
                    <DefaultCard
                        classname={classes.card}
                    >
                        <DefaultButton
                            variant="contained"
                            classname={classes.button}
                            onClick={clickDownloads}
                        >
                            <DownloadIcon
                                classname={classes.icon}
                            />
                            Downloads
                        </DefaultButton>
                    </DefaultCard>
                </Grid2>
                <Grid2 size={12}>
                    <Grid2 container>
                        <Grid2 size={12}>
                            <Typography
                                className={classes.tittle}
                            >
                                Sobre Ns
                            </Typography>
                        </Grid2>
                        <Grid2 size={12}>
                            <Divider variant="fullWidth" />
                        </Grid2>
                    </Grid2>
                </Grid2>
                <Grid2 size={4}>
                    <DefaultCard
                        classname={classes.card}
                    >
                        <DefaultButton
                            variant="contained"
                            classname={classes.button}
                            onClick={clickSobreNos}
                        >
                            <InformationIcon
                                classname={classes.icon}
                            />
                            Sobre Ns
                        </DefaultButton>
                    </DefaultCard>
                </Grid2>
                <Grid2 size={8}>
                    <Typography
                        className={classes.texto}
                    >
                        A ACAP  uma escola de futsal dedicada a desenvolver jovens talentos e a promover o esporte como ferramenta de educao e disciplina. Com uma equipe de profissionais qualificados e apaixonados pelo futsal, oferecemos aulas que estimulam o aprendizado tcnico, o trabalho em equipe e o respeito. Nosso compromisso  formar atletas e cidados, ajudando nossos alunos a alcanarem o seu potencial tanto no esporte quanto na vida. Para saber mais sobre a ns clique no boto ao lado.
                    </Typography>
                </Grid2>
            </Grid2>
        </>
    )
}