import { Divider, Grid2, Typography } from "@mui/material";
import { useSobreNosStyles } from "./sobre-nos-styles"

export const SobreNos = () => {

    const classes = useSobreNosStyles();

    return (
        <>
            <Grid2 container spacing={2} className={classes.root}>
                <Grid2 size={12}>
                    <Grid2 container>
                        <Grid2 size={12}>
                            <Typography
                                className={classes.tittle}
                            >
                                Sobre Ns
                            </Typography>
                        </Grid2>
                        <Grid2 size={12}>
                            <Divider variant="fullWidth" />
                        </Grid2>
                    </Grid2>
                </Grid2>
                <Grid2 size={12} className={classes.containerTexto}>
                    <Typography>
                        A ACAP - Escola de Futsal  uma instituio voltada para a formao e desenvolvimento de jovens talentos no futsal, localizada em So Paulo, no bairro de Vila Prudente. Com sede na Rua Mendona Corte Real, n 03227-110, a escola oferece um ambiente seguro e acolhedor para crianas e adolescentes que desejam aprender e aprimorar suas habilidades no esporte.
                    </Typography>
                    <Typography>
                        A escola  dirigida por Leonardo da Silva Gonalves, responsvel por coordenar as atividades e assegurar a qualidade dos treinos. Leonardo  um profissional dedicado ao desenvolvimento esportivo e  formao de valores nos jovens, promovendo o futsal como uma ferramenta de disciplina, trabalho em equipe e respeito.
                    </Typography>
                    <Typography>
                        Na ACAP - Escola de Futsal, os alunos recebem treinamentos especializados, com foco tanto na tcnica quanto na parte fsica, sempre respeitando os limites e o desenvolvimento de cada idade. A instituio oferece diversas turmas, organizadas por faixa etria, para que cada aluno possa evoluir de maneira progressiva e segura no esporte.
                    </Typography>
                    <Typography>
                        Para mais informaes sobre horrios e matrculas, os interessados podem entrar em contato pelo telefone (11) 97297-3673. A ACAP  o local ideal para aqueles que buscam aprender futsal em um ambiente saudvel, com profissionais experientes e uma estrutura que incentiva o crescimento pessoal e esportivo.
                    </Typography>
                </Grid2>
            </Grid2>
        </>
    )
}